<template>
  <div class="pop">
    <v-dialog v-model="dialog" fullscreen persistent>
      <template v-slot:activator="{ on }">
        <!-- <v-icon color="red lighten-1" large dark v-on="on" @click="enterAdd">mdi-plus-circle</v-icon> -->
        <v-btn class="primary" v-on="on" @click="enterProcess">
          護理紀錄
          <v-icon>mdi-clipboard-list-outline</v-icon>
        </v-btn>
      </template>

      <v-card>
        <v-card-title :class="titleClass" primary-title
          >{{ title }}
          <span
            class="ml-2 white--text pa-1"
            style="border-radius: 5px"
            :class="isShowAll ? 'primary' : 'success'"
            >[{{ isShowAll ? "全部資料" : "90日內資料" }}]</span
          >
          <v-btn class="primary ml-2" @click="importAllNursingRecordData()">載入所有資料</v-btn>
          <v-spacer></v-spacer>
          <v-icon @click="dialog = false">mdi-close</v-icon>
        </v-card-title>

        <v-card-text>
          <v-row>
            <v-col cols="4">
              <v-card>
                <v-card-title class="primary lighten-2 white--text">
                  病歷資料列表
                  <v-spacer></v-spacer>
                </v-card-title>
                <v-card-text class="mt-2">
                  <v-toolbar dense>
                    <v-text-field
                      hide-details
                      prepend-icon="mdi-magnify"
                      single-line
                      label="搜尋"
                      v-model="serachKey"
                    ></v-text-field>
                  </v-toolbar>

                  <div style="overflow: auto">
                    <v-list class="mt-2" height="62vh">
                      <v-list-item-group v-model="model" color="primary">
                        <v-list-item
                          v-for="(item, i) in filterUser"
                          :key="i"
                          @click="selectUser = item"
                        >
                          <v-list-item-icon class="mr-2">
                            <v-icon>mdi-account</v-icon>
                          </v-list-item-icon>
                          <v-list-item-content>
                            <v-list-item-title
                              >{{ item.name }} 床號:{{
                                item.bed_name
                              }}</v-list-item-title
                            >
                            <v-list-item-subtitle
                              >病歷號:{{ item.p_code }}</v-list-item-subtitle
                            >
                          </v-list-item-content>
                        </v-list-item>
                      </v-list-item-group>
                    </v-list>
                  </div>
                </v-card-text>
              </v-card>
            </v-col>

            <v-col cols="8">
              <v-alert color="primary" outlined>
                <div style="overflow: auto">
                  <v-card height="80vh">
                    <v-card-title>
                      <v-text-field
                        label="開始日期"
                        type="date"
                        v-model="start_day"
                        hide-details
                        class="mx-1"
                      ></v-text-field>
                      <v-text-field
                        label="開始時間"
                        type="time"
                        v-model="start_time"
                        hide-details
                        class="mx-1"
                      ></v-text-field>
                      <v-text-field
                        label="結束日期"
                        type="date"
                        v-model="end_day"
                        hide-details
                        class="mx-1"
                      ></v-text-field>
                      <v-text-field
                        label="結束時間"
                        type="time"
                        v-model="end_time"
                        hide-details
                        class="mx-1"
                      ></v-text-field>
                    </v-card-title>
                    <v-card-text>
                      <h3>護理紀錄內容</h3>
                      <v-divider></v-divider>
                      <!-- <pre> {{filterNursingRecordStartEnd()}}</pre> -->
                      <v-alert
                        outlined
                        v-for="(item, index) in filterNursingRecordStartEnd()"
                        :key="index"
                        :class="[{ 'red lighten-4': item.focus == 'true' }]"
                      >
                        <h3>{{ item.date }} {{ item.time }}</h3>
                        <!-- <v-textarea readonly rows="14" :value="item.nursingrecord_content"></v-textarea> -->
                        <v-divider></v-divider>
                        <div style="white-space: pre-wrap">
                          {{ item.nursingrecord_content }}
                        </div>
                      </v-alert>
                      <!-- <div style="overflow:auto">
                        <v-textarea v-model="nursingrecord_content" readonly rows="14"></v-textarea>
                      </div> -->
                    </v-card-text>
                  </v-card>
                </div>
              </v-alert>
            </v-col>
          </v-row>
        </v-card-text>

        <!-- <pre>{{filterNursingPlan()}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      title: "", //抬頭
      titleClass: "", //抬頭顯示樣式
      userItems: [],
      serachKey: "", //病歷搜尋
      model: "",
      selectUser: {}, //選取的病歷
      nursingrecordItems: [], //護理紀錄的資料
      nursingrecord_content: "",
      matchNursingRecord: [], //選取病歷後選出的護理紀錄
      start_day: "",
      end_day: "",
      start_time: "",
      end_time: "",

      isShowAll: false, //是否顯示全部資料
    };
  },

  watch: {
    selectUser() {
      if (Object.keys(this.selectUser).length > 0) {
        this.matchNursingRecord = this.nursingrecordItems.filter((i) => {
          return i.u_snkey == this.selectUser.snkey;
        });

        this.filterNursingRecordStartEnd();
      }
    },
  },

  computed: {
    filterUser() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.userItems);
    },
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("childFn", function (item) {
      this.dialog = true;
      this.enterProcess(item);
    });

    this.getUserData();
    this.getNursingRecordData();
  },

  methods: {
    enterProcess(item) {
      this.title = "護理紀錄一覽";
      this.titleClass = "font-weight-black primary lighten-2";
    },
    //取得所有病歷資料
    getUserData() {
      // this.axios
      //   .post("user/getAll/" + this.$store.state.databaseName)
      this.$api
        .options("user/getAll/" + this.$store.state.databaseName)
        .then((rs) => {
          this.userItems = rs.sort(function (a, b) {
            var a = a.bed_name;
            var b = b.bed_name;

            if (a === b) {
              return 0;
            } else if (a === null) {
              return 1;
            } else if (b === null) {
              return -1;
            } else {
              return a > b ? 1 : -1;
            }
          });

        });
    },
    //取得所有護理紀錄;2023.8.18 調整為90日內的資料
    getNursingRecordData() {
      let start = dayjs().subtract(90, "day").format("YYYY-MM-DD"); //開始日期
      let end = dayjs().format("YYYY-MM-DD"); //結束日期
      let postData = {
        key: `date BETWEEN '${start}' AND '${end}'`,
      };

      let url = `general/searchByWhere/${this.$store.state.databaseName}/nursingrecord`;
      this.$api.options(url, postData).then((rs) => {
        // console.log('newrecordCounts',rs.length)
        var newData = rs.map((i) => {
          var newItem = JSON.parse(i.datalist);
          newItem.u_snkey = i.u_snkey;
          return newItem;
        });

        newData = newData.sort(function (a, b) {
          return a.date > b.date ? 1 : -1;
        });
        this.nursingrecordItems = newData.sort(function (a, b) {
          if (a.date == b.date) {
            return a.time > b.time ? 1 : -1;
          }
        });

        this.isShowAll = false;
      });
    },
    //載入所有資料
    importAllNursingRecordData() {
      this.$confirm("確認載入全部資料?").then((res) => {
        if (res) {
          this.$api.get("nursingrecord").then((rs) => {
            var newData = rs.map((i) => {
              var newItem = JSON.parse(i.datalist);
              newItem.u_snkey = i.u_snkey;
              return newItem;
            });

            newData = newData.sort(function (a, b) {
              return a.date > b.date ? 1 : -1;
            });
            this.nursingrecordItems = newData.sort(function (a, b) {
              if (a.date == b.date) {
                return a.time > b.time ? 1 : -1;
              }
            });

            this.isShowAll = true;
          });
        }
      });
    },
    //取得所有護理紀錄
    // getNursingRecordData() {
    //   this.$api.get("nursingrecord").then((rs) => {
    //     console.log('zone',rs.length)
    //     var newData = rs.map((i) => {
    //       var newItem = JSON.parse(i.datalist);
    //       newItem.u_snkey = i.u_snkey;
    //       return newItem;
    //     });

    //     newData = newData.sort(function (a, b) {
    //       return a.date > b.date ? 1 : -1;
    //     });
    //     this.nursingrecordItems = newData.sort(function (a, b) {
    //       if (a.date == b.date) {
    //         return a.time > b.time ? 1 : -1;
    //       }
    //     });
    //   });
    // },
    
    //選出符合指定時間的護理紀錄
    filterNursingRecordStartEnd() {
      if (
        this.start_day != "" &&
        this.end_day != "" &&
        this.start_time != "" &&
        this.end_time != ""
      ) {
        return this.matchNursingRecord.filter((i) => {
          return (
            dayjs(`${this.start_day} ${this.start_time}`).unix() <=
              dayjs(`${i.date} ${i.time}`).unix() &&
            dayjs(`${this.end_day} ${this.end_time}`).unix() >=
              dayjs(`${i.date} ${i.time}`).unix()
          );
        });
      } else {
        return this.matchNursingRecord;
      }
    },
  },
};
</script>