<template>
  <div class="handoverlist">
    <NursingPlanList class="slideBtn"></NursingPlanList>
    <v-row>
      <v-col cols="12" md="6">
        <h2 class="subheading grey--text">
          <v-icon class="mx-1">mdi-text-box-outline</v-icon>
          <span>交班表</span> 
          <span class="white--text pa-1 ml-2" style="border-radius:5px;" :class="isShowAll ? 'primary' : 'success'">[{{ isShowAll ? '全部資料' : '30日內資料'}}]</span>
        </h2>
      </v-col>
      <v-col cols="12" md="6">
        <!-- 功能表 -->
        <v-toolbar dense>
          <v-btn class="primary" @click="importAllData()">載入所有資料</v-btn>

          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            class="ml-2"
            v-model="serachKey"
          ></v-text-field>

          <popupadd
            @finishAdd="finishAdd"
            @finishEdit="finishEdit"
            ref="enterEdit"
          ></popupadd>
          <!-- <NursingPlanList class="mx-1"></NursingPlanList> -->
        </v-toolbar>
      </v-col>
    </v-row>

    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
      </v-card-title>

      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
        >
          <template v-slot:default="props">
            <v-timeline dense>
              <v-timeline-item
                v-for="(item, index) in props.items"
                :key="index"
                color="blue lighten-2"
                large
              >
                <template v-slot:opposite>
                  <h3 class="font-italic font-weight-medium">
                    {{ item.time | timeFormat }}
                  </h3>
                  <!-- <v-btn class="success" small icon @click="enterEdit(item)">E</v-btn> -->
                </template>
                <v-card class="elevation-2">
                  <v-card-title class="success white--text">
                    紀錄人: {{ item.create_man }}
                    <v-spacer></v-spacer>
                    <v-icon
                      color="white"
                      @click="enterEdit(item)"
                      v-if="checkEdit(item)"
                      >mdi-pencil</v-icon
                    >
                    <v-icon
                      color="white"
                      @click="enterDel(item)"
                      v-if="checkEdit(item)"
                      >mdi-close</v-icon
                    >
                  </v-card-title>
                  <v-card-text>
                    <h3 class="mt-2">交班內容：</h3>
                    <v-textarea
                      class="mt-2"
                      v-model="item.content"
                      readonly
                      filled
                      rows="20"
                    ></v-textarea>
                  </v-card-text>
                </v-card>
              </v-timeline-item>
            </v-timeline>
          </template>
        </v-data-iterator>
      </v-card-text>
    </v-card>

    <pre>
    <!-- {{items}} -->
    </pre>
  </div>
</template>

<script>
import popupadd from "@/views/main/HandoverAdd";
import NursingPlanList from "@/views/main/NursingPlanList";
import dayjs from "dayjs";

export default {
  components: { popupadd, NursingPlanList },
  data() {
    return {
      items: [], //交班資料
      serachKey: "", //搜尋欄
      tempData: [], //儲存前暫存資料庫
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 30,

      isShowAll:false, //是否顯示全部資料
    };
  },

  filters: {
    timeFormat(val) {
      return dayjs(val).format("YYYY-MM-DD HH:mm:ss");
    },
  },

  computed: {
    searchfilter() {
      let keys = this.serachKey.split(" ");
      let str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  methods: {
    checkEdit(item) {
      if (item["create_man"].includes(this.$store.state.pData.username)) {
        return true;
      } else {
        return false;
      }
    },
    //取得所有資料;20238.18 交班表顯示只抓取30日內的資料
    getAllData() {
      let postData = {
        key: '"date":"',
        start: dayjs().subtract(30, 'day').format("YYYY-MM-DD"), //開始日期
        end: dayjs().format("YYYY-MM-DD") //結束日期
      };
      let url = `general/getZoneDataForHandover/${this.$store.state.databaseName}/handover`;
      this.$api.options(url,postData).then((rs) => {
        this.items = rs.sort((a, b) => (a.time < b.time ? 1 : -1));
        this.isShowAll = false
      });
    },
    //載入所有資料
    importAllData(){
      this.$confirm("確認載入全部資料?").then(res=>{
        if (res){
          this.$api.get("handover").then((rs) => {
            this.items = rs.sort((a, b) => (a.time < b.time ? 1 : -1));
            let pop = { msg: "已載入", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.isShowAll = true
          });    
        }
      })
    },
    // 取得所有資料
    // getAllData() {
    //   this.$api.get("handover").then((rs) => {
    //     this.items = rs.sort((a, b) => (a.time < b.time ? 1 : -1));
    //   });
    // },

    enterEdit(item) {
      //進入修改作業 -> 顯示修改頁面
      this.$refs.enterEdit.$emit("childFn", item);
    },
    finishAdd(item) {
      //執行新增(在popupadd執行新增時,回傳後執行的動作,item為傳回的list表單內容)
      item["create_man"] =
        this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")"; //執行動作的人,時間,內容

      this.$api.add("handover", item).then((rs) => {
        if (rs.state == 1) {
          var pop = { msg: "已新增", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.getAllData();
        }
      });
    },
    finishEdit(item) {
      //執行新增(在popupadd執行修改時,回傳後執行的動作,item為傳回的list表單內容)
      item["edit_man"] =
        this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")"; //執行動作的人,時間,內容

      this.$api.post("handover", item).then((rs) => {
        if (rs.state == 1) {
          var pop = { msg: "已修改", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
        }
      });
    },
    enterDel(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "handover",
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.delete('handover',postData)
            .then((rs) => {
              if (rs.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
  },
};
</script>


<style lang="scss">
.handoverlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  .slideBtn {
    position: fixed;
    left: 10px;
    bottom: 10px;
    z-index: 100;
  }
}
</style>