<template>
  <div class="pop">
    <v-dialog v-model="dialog" persistent fullscreen>
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="enterAdd">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleClass" primary-title>{{title}}
          <v-spacer></v-spacer>
          <v-icon @click="dialog=false">mdi-close</v-icon>
        </v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12">
                <!-- <v-text-field
                  label="紀錄時間"
                  prepend-icon="mdi-calendar"
                  v-model="list.time"
                  readonley outlined
                ></v-text-field>-->
                <v-alert type="success">
                  紀錄時間: {{list.time | timeFormat}}
                </v-alert>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="交班內容"
                  prepend-icon="mdi-text-box-outline"
                  v-model="list.content"
                  ref="setfocus"
                  :rules="emptyRules"
                  rows="13"
                  auto-grow
                ></v-textarea>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="error" @click="addOK" v-if="process=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="process=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
        <pre>
          <!-- list:{{list}} -->
        </pre>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //目前操作的表單內容
      oldList: {}, //存放修改前資料
      emptyRules: [v => !!v || "不可空白"],
      title: "", //抬頭
      titleClass: "", //抬頭顯示樣式
      process: "", //儲存執行動作
      loading:false,
    };
  },

  filters: {
    timeFormat(val) {
      return dayjs(val).format("YYYY-MM-DD HH:mm:ss");
    }
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("childFn", function(item) {
      this.dialog = true;
      this.enterEdit(item);
    });
  },

  updated() {
    if (this.list.content.length == 0) {
      this.$nextTick(() => {
        this.$refs.setfocus.focus();
        this.$refs.form.resetValidation();
      });
    }
  },

  methods: {
    enterAdd() {
      //執行新增流程
      this.process = "add";
      this.title = "新增交班表";
      this.titleClass = "font-weight-black error lighten-2";
      this.list = {
        time: new Date().toISOString(),
        content : "",
      };
    },
    enterEdit(item) {
      //執行修改流程
      this.process = "edit";
      this.title = "修改交班表";
      this.titleClass = "font-weight-black success lighten-2";
      this.oldList = item; //暫存未修改前資料
      this.list = { ...item }; //列表中複製一份顯示及修改用資料
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.loading = true;
        this.$emit("finishAdd", this.list);
        this.dialog = false;
        this.loading = false;
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.loading = true;
        for (var i in this.list) {
          //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
          this.oldList[i] = this.list[i];
        }
        this.dialog = false;
        this.$emit("finishEdit", this.list);
        this.loading = false;
      }
    }
  }
};
</script>

